import { useDispatch } from 'react-redux';
import taskManagerActionCreators from '../../store/actionCreators/taskManager';
import { getDateLocaleString } from '../../utils/utils';
import Modal from '../UI/Modal/Modal';
import ModalButton from '../UI/ModalButton/ModalButton';
import { RemoveTaskConfirmText, RemoveTaskDescriptionBox, RemoveTaskDescriptionItem } from './RemoveTaskModal.styled';

const RemoveTaskModal = ({ task, setActive }) => {
	const dispatch = useDispatch();
	const removeTask = () => {
		dispatch(taskManagerActionCreators.remove(task.id));
		setActive(false);
	};
	
	return (
		<Modal title=" " setActive={setActive}>
			<RemoveTaskDescriptionBox>
				<RemoveTaskDescriptionItem>: {task.text}</RemoveTaskDescriptionItem>
				<RemoveTaskDescriptionItem>: {getDateLocaleString(task.createdAt)}</RemoveTaskDescriptionItem>
				{task.completedAt && <RemoveTaskDescriptionItem> : {getDateLocaleString(task.completedAt)}</RemoveTaskDescriptionItem>}
				<RemoveTaskConfirmText> ,     ?   .</RemoveTaskConfirmText>
			</RemoveTaskDescriptionBox>
			<ModalButton onClick={removeTask}></ModalButton>
		</Modal>
	);
};

export default RemoveTaskModal;